@import "./variables.css";

.projects {
  padding: var(--space-16) var(--space-6);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  width: 100%;
  box-sizing: border-box;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
}

.projects h1,
.projects h2,
.projects h3,
.projects h4,
.projects h5,
.projects h6 {
  text-wrap: balance;
}

.projects h2 {
  text-align: center;
  font-size: clamp(1.5rem, 3vw + 0.5rem, 2.25rem);
  line-height: clamp(1.2, 0.3vw + 1.15, 1.3);
  margin-bottom: var(--space-12);
  font-family: var(--font-heading);
  letter-spacing: var(--tracking-wide);
}

.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-8);
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: var(--breakpoint-md)) {
  .project-list {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: var(--space-6);
  }
}

@media (max-width: var(--breakpoint-md)) {
  .projects {
    padding: var(--space-12) var(--space-4);
  }

  .projects-container {
    padding: 0;
  }

  .projects h2 {
    margin-bottom: var(--space-8);
    font-size: clamp(1.5rem, 6vw, 2rem);
  }

  .project-list {
    grid-template-columns: 1fr;
    gap: var(--space-6);
    max-width: 100%;
    padding: 0;
  }

  .project-card {
    margin: 0;
    width: 100%;
    max-width: 100%;
  }

  .project-card img {
    height: 200px;
    object-fit: cover;
  }

  .project-card h3 {
    font-size: clamp(1.125rem, 5vw, 1.5rem);
    padding: var(--space-4) var(--space-4) 0 var(--space-4);
  }

  .project-card p {
    padding: var(--space-2) var(--space-4);
    font-size: clamp(0.875rem, 4vw, 1rem);
    line-height: 1.6;
  }

  .project-tech-stack {
    padding: var(--space-2) var(--space-4);
    flex-wrap: wrap;
    gap: var(--space-2);
  }

  .project-links {
    padding: var(--space-3) var(--space-4) var(--space-4) var(--space-4);
    gap: var(--space-3);
  }
}

/* ===== PROJECTS RESPONSIVE & MOBILE OPTIMIZATIONS ===== */

/* Mobile touch targets */
@media (max-width: 768px) {
  .project-links {
    display: flex;
    flex-direction: row;
    gap: var(--space-3);
    flex-wrap: wrap;
  }
  
  .project-card a {
    min-height: 44px;
    padding: var(--space-3) var(--space-4);
    font-size: var(--text-base);
    flex: 1;
    display: flex !important;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .projects {
    padding-left: var(--space-3);
    padding-right: var(--space-3);
  }
  
  /* Stack buttons side by side on small screens */
  .project-links {
    flex-direction: row !important;
    width: 100%;
    gap: var(--space-2);
    flex-wrap: wrap;
  }

  .project-links > * {
    flex: 1 1 calc(50% - var(--space-1));
    min-width: 120px;
  }
  
  .project-card a {
    width: 100% !important;
    display: flex !important;
    justify-content: center;
    align-items: center;
    font-size: var(--text-sm);
  }
}

/* Hover effects for touch devices */
@media (hover: none) and (pointer: coarse) {
  .project-card:hover {
    transform: none;
  }
  
  .project-card:active {
    transform: scale(0.98);
    transition: transform 0.1s ease-out;
  }
}

@media (max-width: var(--breakpoint-sm)) {
  .projects {
    padding: var(--space-10) var(--space-3);
  }

  .projects h2 {
    font-size: clamp(1.25rem, 7vw, 1.75rem);
    margin-bottom: var(--space-6);
  }

  .project-list {
    grid-template-columns: 1fr;
    gap: var(--space-5);
  }

  .project-badges {
    gap: var(--space-1-5);
  }

  .project-card::before {
    animation: none;
  }

  .project-card:hover img,
  .project-card:focus-within img {
    transform: scale(1.03);
  }

  .project-card img {
    height: 180px;
  }

  .project-card h3 {
    font-size: clamp(1.0625rem, 5.5vw, 1.375rem);
    padding: var(--space-4) var(--space-4) 0 var(--space-4);
  }

  .project-card p {
    padding: var(--space-2) var(--space-4);
    font-size: clamp(0.875rem, 4.5vw, 1rem);
    line-height: 1.6;
  }

  .project-tech-stack {
    padding: var(--space-2) var(--space-4);
    gap: var(--space-1-5);
  }

  .project-links {
    padding: var(--space-3) var(--space-4) var(--space-4) var(--space-4);
    flex-direction: column;
    gap: var(--space-2-5);
  }

  .project-links a {
    width: 100%;
    justify-content: center;
    padding: var(--space-3) var(--space-4);
  }
}

@media (max-width: var(--breakpoint-xs)) {
  .projects {
    padding: var(--space-8) var(--space-2-5);
  }

  .projects h2 {
    font-size: clamp(1.125rem, 8vw, 1.5rem);
    margin-bottom: var(--space-5);
  }

  .project-card:hover .project-image-wrapper,
  .project-card:focus-within .project-image-wrapper {
    transform: none;
  }

  .project-card img {
    height: 160px;
  }

  .project-card h3 {
    font-size: clamp(1rem, 6vw, 1.25rem);
    padding: var(--space-3) var(--space-3) 0 var(--space-3);
  }

  .project-card p {
    padding: var(--space-2) var(--space-3);
    font-size: clamp(0.8125rem, 4.5vw, 0.9375rem);
  }

  .project-tech-stack {
    padding: var(--space-2) var(--space-3);
    gap: var(--space-1);
  }

  .project-links {
    padding: var(--space-2-5) var(--space-3) var(--space-3) var(--space-3);
    gap: var(--space-2);
  }

  .project-card a {
    font-size: var(--text-sm);
    padding: var(--space-2-5) var(--space-3);
  }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  .project-card::before {
    animation: none;
  }

  .project-card:hover img,
  .project-card:focus-within img {
    transform: scale(1.05);
  }

  .project-card:hover .project-image-wrapper,
  .project-card:focus-within .project-image-wrapper {
    transform: none;
  }

  .project-image-overlay {
    transition: opacity var(--duration-200) var(--ease-out);
  }

  .project-card a {
    transition: color var(--duration-200) var(--ease-out);
  }

  .project-card a:hover,
  .project-card a:focus {
    transform: none;
  }
}

.project-card {
  background: var(--bg-elevated);
  padding: 0;
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-secondary);
  box-shadow: var(--shadow-md);
  transition: var(--transition-base);
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  overflow: visible;
}



/* Project Badges Container */
.project-badges {
  display: flex;
  gap: var(--space-2);
  flex-wrap: wrap;
  padding: var(--space-4);
}

/* Category-specific colors */
.tag-category.category-web-app {
  background-color: rgba(59, 130, 246, 0.15);
  border-color: rgba(59, 130, 246, 0.5);
}

.tag-category.category-web-app:hover {
  background-color: rgba(59, 130, 246, 0.25);
  border-color: rgba(59, 130, 246, 0.8);
}

.tag-category.category-ai {
  background-color: rgba(139, 92, 246, 0.15);
  border-color: rgba(139, 92, 246, 0.5);
}

.tag-category.category-ai:hover {
  background-color: rgba(139, 92, 246, 0.25);
  border-color: rgba(139, 92, 246, 0.8);
}

/* Status Indicator */
.project-status {
  display: inline-flex;
  align-items: center;
  background: var(--bg-glass);
  backdrop-filter: blur(var(--blur-sm));
  -webkit-backdrop-filter: blur(var(--blur-sm));
  padding: var(--space-1) var(--space-2);
  border-radius: var(--radius-full);
  font-size: var(--text-xs);
  font-weight: var(--font-semibold);
  border: 1px solid var(--border-secondary);
  color: var(--text-primary);
}

.status-dot {
  display: inline-block;
  width: var(--space-2);
  height: var(--space-2);
  border-radius: var(--radius-full);
  margin-right: var(--space-1);
}

.project-status.status-live .status-dot {
  background-color: var(--color-accent-green);
  box-shadow: 0 0 8px rgba(16, 185, 129, 0.6);
}

.project-status.status-in-progress .status-dot {
  background-color: var(--color-accent-yellow);
  box-shadow: 0 0 8px rgba(245, 158, 11, 0.6);
}

.project-status.status-completed .status-dot {
  background-color: var(--color-accent-blue);
  box-shadow: 0 0 8px rgba(59, 130, 246, 0.6);
}

/* Image Wrapper with Glassmorphism */
.project-image-wrapper {
  position: relative;
  overflow: hidden;
  border-radius: var(--radius-sm);
  perspective: 1000px;
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform var(--duration-300) var(--ease-out);
  display: block;
  transform-style: preserve-3d;
}

.project-image-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    rgba(139, 92, 246, 0.2),
    rgba(59, 130, 246, 0.2)
  );
  opacity: 0;
  transition: var(--transition-base);
  pointer-events: none;
}

.project-card h3 {
  margin: 0 0 var(--space-3) 0;
  padding: var(--space-5) var(--space-5) 0 var(--space-5);
  font-size: clamp(1rem, 1.5vw + 0.5rem, 1.25rem);
  line-height: clamp(1.2, 0.3vw + 1.15, 1.25);
  font-family: var(--font-heading);
  font-weight: var(--font-bold);
  color: var(--text-primary);
}

.project-card p {
  flex-grow: 1;
  margin: 0 0 var(--space-4) 0;
  padding: 0 var(--space-5);
  color: var(--text-secondary);
  font-size: clamp(0.8125rem, 0.8vw + 0.5rem, 0.875rem);
  line-height: clamp(1.5, 0.5vw + 1.4, 1.625);
  word-break: keep-all; /* Prevent mid-word breaks */
  overflow-wrap: break-word; /* Break long technical terms at appropriate points */
}

.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-2); /* Consistent gaps across breakpoints */
  margin: 0 0 var(--space-5) 0;
  padding: 0 var(--space-5);
}

.project-tech-stack .tag {
  padding: var(--space-1) var(--space-2-5);
  background-color: var(--bg-surface);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  color: var(--text-secondary);
  font-weight: var(--font-medium);
  white-space: nowrap; /* Prevent tech stack tags from wrapping */
  min-width: fit-content; /* Ensure tags maintain minimum width */
}

.project-links {
  display: flex;
  gap: var(--space-4);
  flex-wrap: wrap;
  padding: var(--space-4) var(--space-5) var(--space-5) var(--space-5);
  margin-top: auto;
  border-top: 1px solid var(--border-primary);
  background: var(--bg-surface);
}

.project-card a {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
  color: var(--text-accent);
  text-decoration: none;
  font-weight: var(--font-medium);
  font-size: var(--text-sm);
  position: relative;
  transition: var(--transition-base), transform var(--haptic-duration) var(--ease-out);
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-md);
  overflow: hidden;
  will-change: transform;
  white-space: nowrap; /* Prevent project link text from wrapping */
  min-width: fit-content;
}

.project-card a::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: var(--radius-full);
  background: var(--ripple-color);
  transform: translate(-50%, -50%);
  transition: width var(--ripple-duration) ease-out, height var(--ripple-duration) ease-out;
  pointer-events: none;
  z-index: 0;
}

.project-card a .link-icon {
  font-size: var(--text-sm);
  transition: transform var(--duration-300) var(--ease-out);
  position: relative;
  z-index: 1;
}

.project-card a::after {
  content: "";
  position: absolute;
  left: var(--space-3);
  bottom: var(--space-1);
  width: 0;
  height: 2px;
  background: var(--text-accent);
  transition: var(--transition-transform);
  z-index: 1;
}

.project-card a:hover,
.project-card a:focus {
  color: var(--color-accent-blue);
  transform: translateY(-2px) scale(var(--haptic-scale-up));
  box-shadow: var(--shadow-md), var(--shadow-glow-sm);
}

.project-card a:active {
  transform: translateY(0) scale(var(--haptic-scale-down));
  box-shadow: var(--haptic-shadow-press);
}

.project-card a:active::before {
  width: var(--ripple-size);
  height: var(--ripple-size);
  transition: width 0s, height 0s;
}

.project-card a:hover::after,
.project-card a:focus::after {
  width: calc(100% - var(--space-6));
}

.project-card a:hover .link-icon,
.project-card a:focus .link-icon {
  transform: translateX(2px);
}

.project-card:hover,
.project-card:focus-within {
  transform: translateY(-var(--space-2)) scale(1.02);
  border-color: var(--border-focus);
  box-shadow: var(--shadow-xl), var(--shadow-glow-md);
}



.project-card:hover .project-image-wrapper,
.project-card:focus-within .project-image-wrapper {
  transform: rotateX(var(--rotate-x, 0deg)) rotateY(var(--rotate-y, 0deg));
  transition: transform var(--duration-200) var(--ease-out);
}

.project-card:hover img,
.project-card:focus-within img {
  transform: scale(1.08) rotateX(2deg) rotateY(2deg);
}

.project-card:hover .project-image-overlay,
.project-card:focus-within .project-image-overlay {
  opacity: 1;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
