body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1;
  text-rendering: optimizeLegibility;
  /* Prevent horizontal scrolling on mobile */
  overflow-x: hidden;
  /* Improve touch scrolling on iOS */
  -webkit-overflow-scrolling: touch;
}

/* ===== CUSTOM SCROLLBAR DESIGN ===== */

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(211, 238, 229, 0.3) rgba(26, 26, 26, 0.5);
}

/* Webkit browsers (Chrome, Safari, Edge) */
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: rgba(26, 26, 26, 0.5);
  border-radius: 10px;
  margin: 4px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, rgba(211, 238, 229, 0.3), rgba(145, 212, 190, 0.4));
  border-radius: 10px;
  border: 2px solid rgba(26, 26, 26, 0.5);
  transition: background 0.3s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, rgba(211, 238, 229, 0.5), rgba(145, 212, 190, 0.6));
  box-shadow: 0 0 10px rgba(211, 238, 229, 0.3);
}

::-webkit-scrollbar-thumb:active {
  background: linear-gradient(135deg, rgba(211, 238, 229, 0.7), rgba(145, 212, 190, 0.8));
}

/* Scrollbar corner (where horizontal and vertical scrollbars meet) */
::-webkit-scrollbar-corner {
  background: rgba(26, 26, 26, 0.5);
}

code {
  font-family: 'JetBrains Mono', 'SF Mono', 'Monaco', 'Inconsolata', 'Consolas', 'Courier New',
    monospace;
  font-feature-settings: 'liga' 1, 'calt' 1;
}

/* Improve focus visibility for better accessibility */
*:focus {
  outline: 2px solid var(--color-secondary-400);
  outline-offset: 2px;
}

/* Ensure smooth scrolling works on all browsers */
html {
  scroll-behavior: smooth;
}

/* Improve text selection */
::selection {
  background-color: var(--color-secondary-400);
  color: var(--text-inverse);
}

::-moz-selection {
  background-color: var(--color-secondary-400);
  color: var(--text-inverse);
}
